<template>
   <div class="min-body">
      <!--添加用户-->
      <div class="add-user-box">
         <div class="ict-query-button" @click="isDialogVisible = true">添加用户</div>
      </div>
      <!--表格展示-->
      <!--tabel展示-->
      <div class="user-tabel-box">
         <el-table
                 v-loading="loading"
                 border
                 :data="userData"
                 :header-cell-style="{
                            background: '#f6f6f6',
                            width: '1184px',
                            height: '54px',
                            lineHeight: '54px',
                            fontSize: '14px',
                            color: 'rgba(0, 0, 0, 0.85)',
                        }">
            <el-table-column  label="序号" width="50" >
               <template slot-scope="scope">
                  {{scope.$index+1}}
               </template>
            </el-table-column>
            <el-table-column prop="user" label="用户名" />
            <el-table-column prop="phone" label="手机号" />
            <el-table-column prop="status" label="级别" />
            <el-table-column prop="time" label="创建时间" />
            <el-table-column label="操作" width="85">
               <template slot-scope="scope">
                  <el-button
                          type="text"
                          size="small"
                  >删除</el-button>
                  <el-button
                          type="text"
                          size="small"
                          @click="handleClick(scope.row)"
                  >重置</el-button>
               </template>
            </el-table-column>
         </el-table>
      </div>
      <!--添加用户-->
      <div class="zhb-useradd-dialog">
         <el-dialog
                 title="添加用户"
                 :visible.sync="isDialogVisible"
                 width="500px"
                 top="62px"
         >
            <!--main-->
            <div class="ict-dialog-add">
               <el-form
                       :model="addForm.ruleForm"
                       :rules="addForm.rules"
                       ref="addForm.ruleForm"
               >
                  <!--用户名-->
                  <el-form-item prop="userName">
                     <div class="collect-add-row">
                        <div class="collect-add-title">
                           <span>用户名:</span><i>*</i>
                        </div>
                        <div class="collect-add-con">
                           <el-input
                                   type="text"
                                   v-model="addForm.ruleForm.userName"
                                   placeholder="请输入"
                           ></el-input>
                        </div>
                     </div>
                  </el-form-item>
                  <!--密码-->
                  <el-form-item prop="passWord">
                     <div class="collect-add-row">
                        <div class="collect-add-title">
                           <span>密码:</span><i  style="left: 12px;">*</i>
                        </div>
                        <div class="collect-add-con">
                           <el-input
                                   type="text"
                                   v-model="addForm.ruleForm.passWord"
                                   placeholder="请输入"
                           ></el-input>
                        </div>
                     </div>
                  </el-form-item>

                  <!--级别-->
                  <el-form-item prop="status">
                     <div class="collect-add-row">
                        <div class="collect-add-title" style="margin-top: 8px;">
                           <span>级别:</span><i style="left: 12px;">*</i>
                        </div>
                        <div class="collect-add-con">
                           <el-radio v-model="addForm.ruleForm.status" label="1" style="margin-right: 10px;">管理员</el-radio>
                           <el-radio v-model="addForm.ruleForm.status" label="2">普通用户</el-radio>
                        </div>
                     </div>
                  </el-form-item>


               </el-form>
            </div>
            <!--操作按钮-->
            <span slot="footer" class="dialog-footer">
               <div class="ict-query-button" @click="addUser('addForm.ruleForm')">确 定</div>
               <div class="ict-query-button cz"  @click="isDialogVisible = false">取 消</div>
            </span>
         </el-dialog>
      </div>


   </div>
</template>

<script>
    export default {
        name: "user",
        data: function () {
            return {
               userData:[
                  {user:"姜磊",phone:"13134245678",status:"管理员",time:"2021-08-04 17:45:31"},
                  {user:"李新",phone:"15874521745",status:"管理员",time:"2021-08-04 17:45:31"},
                  {user:"姜磊",phone:"13134245678",status:"普通用户",time:"2021-08-04 17:45:31"},
                  {user:"姜磊",phone:"13134245678",status:"普通用户",time:"2021-08-04 17:45:31"},

               ],
               loading:false,
               //添加用户
               isDialogVisible:false,
               //添加用户弹框
               addForm: {
                  //数据存储
                  ruleForm: {
                     userName: "", //用户名
                     passWord:"",//密码
                     status:''
                  },
                  //必填项校验
                  rules: {
                     userName: [
                        { required: true, message: "请输入用户名", trigger: "blur" },
                     ],
                     passWord: [
                        { required: true, message: "请输入密码", trigger: "blur" },
                     ],
                     phone: [
                        { required: true, message: "请输入手机号码", trigger: "blur" },
                     ],
                     status:[
                        { required: true, message: "请选择级别", trigger: "blur" },
                     ]
                  },
               },
            }
        },
        methods: {
           addUser(formName){
              const that = this;
              that.$refs[formName].validate((valid) => {
                 if (valid) {
                    // if (that.addForm.ruleForm.status == "") {
                    //    that.$message.error("级别为必填项！");
                    //    console.log(that.addForm.ruleForm);
                    // }
                 } else {
                    this.$message.error("有必填项未填！");
                 }
              });
           }
        }
    }
</script>

<style lang="scss" scoped>
   @import "./user";

</style>
<style lang="scss">
   .zhb-useradd-dialog{
      .el-input{
         display: flex!important;
      }
      .el-input__inner{
         height: 32px!important;
         line-height: 32px!important;
      }
      .el-form-item__error{
         left: 120px;
      }
      .el-form-item{
         margin-bottom: 15px;
      }
   }
</style>
